/**
 * @module M/plugin/AddServices
 */
import 'assets/css/addservices';
import AddServicesControl from './addservicescontrol';

export default class AddServices extends M.Plugin {
  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @api stable
   */
  constructor(options) {
    super();
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    /**
     * Position of the plugin on browser window
     * @private
     * @type {String}
     * Possible values: 'TL', 'TR', 'BR', 'BL'
     */
    this.position = options.position || 'TR';
  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    const control = new AddServicesControl();
    control.on(M.evt.ADDED_TO_MAP, () => {
      this.fire(M.evt.ADDED_TO_MAP);
    });
    this.controls_.push(control);
    this.map_ = map;
    this.panel_ = new M.ui.Panel('panelAddServices', {
      collapsible: true,
      className: 'm-addservices',
      collapsedButtonClass: 'g-cartografia-capas',
      position: M.ui.position[this.position],
      tooltip: 'Cargar WMS',
    });
    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
  }

  /**
   * This function destroys this plugin
   *
   * @public
   * @function
   * @api stable
   */
  destroy() {
    this.map_.removePanel(this.panel_);
    this.map_ = null;
    this.controls_ = null;
    this.panel_ = null;
  }
}
